body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* overflow-x: hidden; */
}

.containeras {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(27, 18, 18); */
  width: 100%;
}

:root {
  --primary_cyan: hsl(180, 66%, 49%);
  --primary_cyan_light: hsl(180, 66%, 49%, 0.7);
  --primart_dark_violet: hsl(257, 27%, 26%);
  --secondary_red: hsl(0, 87%, 67%);
  --gray: hsl(0, 0%, 75%);
  --grayish_violet: hsl(257, 7%, 63%);
  --dark_blue: hsl(255, 11%, 22%);
  --dark_violet: hsl(260, 8%, 14%);
  --white: #fff;
}

.navWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: hsl(257, 7%, 63%);
  color: var(--grayish_violet);
  font-weight: 700;
  margin-top: 30px;
}

.firstSection {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 400px;
  justify-content: space-between;
}

.firstSection p:hover {
  color: hsl(260, 8%, 14%);
  color: var(--dark_violet);
  cursor: pointer;
}
.secondSection {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 170px;
}

.secondSection p:hover {
  color: hsl(260, 8%, 14%);
  color: var(--dark_violet);
  cursor: pointer;
}

.menu {
  display: none;
}

.show {
  display: none;
}
.hamburgerMenu {
  display: block;
  background-color: hsl(257, 27%, 26%);
  background-color: var(--primart_dark_violet);
  color: #fff;
  color: var(--white);
  margin-top: 22px;
  border-radius: 7px;
  z-index: -1;
}

.hamburgerMenu hr {
  width: 90%;
  justify-content: center;
  display: flex;
  align-items: center;
  text-align: center;
  margin-top: 12px;
  color: #f2f2f2;
}

.hamburgerMenu div {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Poppins";
  font-weight: 700;
}
.hamburgerMenu div p {
  margin-top: 12px;
  margin-bottom: 12px;
}
.hamburgerMenu button {
  margin-bottom: 22px;
  background-color: hsl(180, 66%, 49%);
  background-color: var(--primary_cyan);
  color: #fff;
  color: var(--white);
  border: none;
  padding: 10px;
  border-radius: 55px;
  width: 70%;
  margin-left: 22px;
  font-weight: 700;
}

@media only screen and (max-width: 607px) {
  .firstSection p,
  .secondSection {
    display: none;
  }
  .menu {
    display: block;
  }
}

@media only screen and (max-width: 375px) {
  .firstSection p,
  .secondSection {
    display: none;
  }
  .menu {
    display: block;
  }
}

