}

button {
  background-color: hsl(180, 66%, 49%);
  background-color: var(--primary_cyan);
  color: var(--white);
  border: none;
  padding: 10px;
  font-weight: 700;
}

button:hover {
  background-color: hsl(180, 66%, 49%, 0.7);
  background-color: var(--primary_cyan_light);
  cursor: pointer;
}

